import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import { Button } from "./ui/button";
import { Badge } from "./ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "./ui/tabs";
import { Input } from "./ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "./ui/select";
import {
  Calendar,
  Plus,
  CheckCircle,
  AlertTriangle,
  Sparkles,
  Star,
  ChevronLeft,
  ChevronRight,
} from "lucide-react";

export function MenuPlanning() {
  const [selectedDate, setSelectedDate] = useState(4);
  const [selectedMonth] = useState(8);
  const [newItemInput, setNewItemInput] = useState("");
  const [selectedCategory, setSelectedCategory] = useState("");

  //   (8 )
  const calendarData: {
    [key: number]: { status: string; hasSpecial: boolean };
  } = {
    1: { status: "completed", hasSpecial: false },
    2: { status: "completed", hasSpecial: false },
    3: { status: "completed", hasSpecial: true },
    4: { status: "completed", hasSpecial: false },
    5: { status: "pending", hasSpecial: false },
    6: { status: "empty", hasSpecial: false },
    7: { status: "empty", hasSpecial: false },
    // ...  
  };

  //   
  const [menuItems, setMenuItems] = useState({
    4: {
      breakfast: ["", "", "", "", ""],
      lunch: ["", "", "", "", ""],
      dinner: ["", "", "", "", ""],
      special: null,
    },
  });

  const currentMenu = menuItems[selectedDate as keyof typeof menuItems] || {
    breakfast: [],
    lunch: [],
    dinner: [],
    special: null,
  };

  //   
  const recommendedSpecials = [
    { name: "", reason: " ", popularity: 95, season: "" },
    { name: "", reason: " ", popularity: 88, season: "" },
    { name: "", reason: " ", popularity: 82, season: "" },
    { name: "", reason: " ", popularity: 78, season: "" },
  ];

  // AI  
  const aiRecommendations = {
    breakfast: ["", "", ""],
    lunch: ["", "", ""],
    dinner: ["", "", ""],
  };

  const getDaysInMonth = (year: number, month: number) => {
    return new Date(year, month, 0).getDate();
  };

  const getCalendarDays = () => {
    const daysInMonth = getDaysInMonth(2025, selectedMonth);
    const firstDay = new Date(2025, selectedMonth - 1, 1).getDay();
    const days = [];

    //    
    for (let i = 0; i < firstDay; i++) {
      days.push(null);
    }

    //   
    for (let day = 1; day <= daysInMonth; day++) {
      days.push(day);
    }

    return days;
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case "completed":
        return <CheckCircle className="w-4 h-4 text-secondary" />;
      case "pending":
        return <AlertTriangle className="w-4 h-4 text-warning" />;
      default:
        return null;
    }
  };

  //    
  const updateMenuItem = (
    mealType: "breakfast" | "lunch" | "dinner",
    index: number,
    value: string
  ) => {
    setMenuItems((prev) => ({
      ...prev,
      [selectedDate]: {
        ...prev[selectedDate as keyof typeof prev],
        [mealType]: prev[selectedDate as keyof typeof prev][mealType].map(
          (item, i) => (i === index ? value : item)
        ),
      },
    }));
  };

  //    
  const removeMenuItem = (
    mealType: "breakfast" | "lunch" | "dinner",
    index: number
  ) => {
    setMenuItems((prev) => ({
      ...prev,
      [selectedDate]: {
        ...prev[selectedDate as keyof typeof prev],
        [mealType]: prev[selectedDate as keyof typeof prev][mealType].filter(
          (_, i) => i !== index
        ),
      },
    }));
  };

  //    
  const addMenuItem = (mealType: "breakfast" | "lunch" | "dinner") => {
    if (newItemInput.trim()) {
      setMenuItems((prev) => ({
        ...prev,
        [selectedDate]: {
          ...prev[selectedDate as keyof typeof prev],
          [mealType]: [
            ...prev[selectedDate as keyof typeof prev][mealType],
            newItemInput.trim(),
          ],
        },
      }));
      setNewItemInput("");
      setSelectedCategory("");
    }
  };

  // AI    
  const addAIRecommendation = (
    mealType: "breakfast" | "lunch" | "dinner",
    item: string
  ) => {
    setMenuItems((prev) => ({
      ...prev,
      [selectedDate]: {
        ...prev[selectedDate as keyof typeof prev],
        [mealType]: [
          ...prev[selectedDate as keyof typeof prev][mealType],
          item,
        ],
      },
    }));
  };

  //   
  const MenuInputSection = ({
    mealType,
    mealTypeKor,
  }: {
    mealType: "breakfast" | "lunch" | "dinner";
    mealTypeKor: string;
  }) => (
    <TabsContent value={mealType} className="space-y-4">
      <div className="space-y-4">
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-3">
          {currentMenu[mealType].map((item, index) => (
            <div key={index} className="flex items-center gap-2">
              <Input
                value={item}
                onChange={(e) =>
                  updateMenuItem(mealType, index, e.target.value)
                }
                className="flex-1"
              />
              <Button
                variant="outline"
                size="sm"
                onClick={() => removeMenuItem(mealType, index)}
              >
                
              </Button>
            </div>
          ))}
        </div>

        <div className="flex gap-2">
          <Select value={selectedCategory} onValueChange={setSelectedCategory}>
            <SelectTrigger className="flex-1">
              <SelectValue placeholder="  " />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="soup"></SelectItem>
              <SelectItem value="main"></SelectItem>
              <SelectItem value="side"></SelectItem>
              <SelectItem value="rice">/</SelectItem>
            </SelectContent>
          </Select>
          <Input
            placeholder=" "
            className="flex-1"
            value={newItemInput}
            onChange={(e) => setNewItemInput(e.target.value)}
            onKeyPress={(e) => {
              if (e.key === "Enter") {
                addMenuItem(mealType);
              }
            }}
          />
          <Button variant="outline" onClick={() => addMenuItem(mealType)}>
            
          </Button>
        </div>

        <div className="flex gap-2">
          <Button variant="outline" className="flex-1">
            <Sparkles className="w-4 h-4 mr-2" />
            AI  
          </Button>
        </div>

        {/* AI    */}
        <div className="p-4 bg-info/10 rounded-lg">
          <h4 className="text-sm font-medium mb-2 text-info">
            AI  {mealTypeKor} 
          </h4>
          <div className="flex flex-wrap gap-2">
            {aiRecommendations[mealType].map((item, index) => (
              <Badge
                key={index}
                variant="outline"
                className="cursor-pointer hover:bg-info/20"
                onClick={() => addAIRecommendation(mealType, item)}
              >
                {item}
              </Badge>
            ))}
          </div>
        </div>
      </div>
    </TabsContent>
  );

  return (
    <div className="space-y-6 container-main">
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1> </h1>
          <p className="text-muted-foreground">
                 
          </p>
        </div>
        <div className="flex gap-2">
          <Button variant="outline" size="sm">
              
            <Badge className="ml-2 bg-warning">3</Badge>
          </Button>
          <Button className="gradient-primary text-white border-0">
            <Plus className="w-4 h-4 mr-2" />
              
          </Button>
        </div>
      </div>

      {/*  2  */}
      <div className="grid grid-cols-1 lg:grid-cols-5 gap-6 min-h-[600px]">
        {/*  -   */}
        <div className="lg:col-span-2">
          <Card className="elevation-1 h-full">
            <CardHeader>
              <div className="flex items-center justify-between">
                <CardTitle className="flex items-center gap-2">
                  <Calendar className="w-5 h-5 text-primary" />
                  2025 {selectedMonth}  
                </CardTitle>
                <div className="flex items-center gap-2">
                  <Button variant="outline" size="sm">
                    <ChevronLeft className="w-4 h-4" />
                  </Button>
                  <Button variant="outline" size="sm">
                    <ChevronRight className="w-4 h-4" />
                  </Button>
                </div>
              </div>
            </CardHeader>
            <CardContent className="p-4">
              {/*   */}
              <div className="grid grid-cols-7 gap-1 mb-2">
                {["", "", "", "", "", "", ""].map((day) => (
                  <div
                    key={day}
                    className="p-2 text-center text-sm font-medium text-muted-foreground"
                  >
                    {day}
                  </div>
                ))}
              </div>

              {/*   */}
              <div className="grid grid-cols-7 gap-1">
                {getCalendarDays().map((day, index) => (
                  <div
                    key={index}
                    className={`
                      p-2 min-h-[60px] border rounded-lg cursor-pointer transition-all
                      ${!day ? "opacity-30" : "hover:bg-accent/50"}
                      ${
                        day === selectedDate
                          ? "bg-primary/20 border-primary"
                          : "border-border"
                      }
                    `}
                    onClick={() => day && setSelectedDate(day)}
                  >
                    {day && (
                      <div>
                        <div className="flex items-center justify-between mb-1">
                          <span className="text-sm font-medium">{day}</span>
                          {calendarData[day] &&
                            getStatusIcon(calendarData[day].status)}
                        </div>
                        {calendarData[day]?.hasSpecial && (
                          <Star className="w-3 h-3 text-warning" />
                        )}
                      </div>
                    )}
                  </div>
                ))}
              </div>

              {/*  */}
              <div className="mt-4 space-y-2 text-xs">
                <div className="flex items-center gap-2">
                  <CheckCircle className="w-4 h-4 text-secondary" />
                  <span> </span>
                </div>
                <div className="flex items-center gap-2">
                  <AlertTriangle className="w-4 h-4 text-warning" />
                  <span> </span>
                </div>
                <div className="flex items-center gap-2">
                  <Star className="w-4 h-4 text-warning" />
                  <span> </span>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/*  -    */}
        <div className="lg:col-span-3">
          <Card className="elevation-1 h-full">
            <CardHeader>
              <CardTitle>8 {selectedDate}  </CardTitle>
            </CardHeader>
            <CardContent className="p-6">
              <Tabs defaultValue="breakfast" className="space-y-6">
                <TabsList className="grid w-full grid-cols-3">
                  <TabsTrigger value="breakfast"></TabsTrigger>
                  <TabsTrigger value="lunch"></TabsTrigger>
                  <TabsTrigger value="dinner"></TabsTrigger>
                </TabsList>

                <MenuInputSection mealType="breakfast" mealTypeKor="" />
                <MenuInputSection mealType="lunch" mealTypeKor="" />
                <MenuInputSection mealType="dinner" mealTypeKor="" />
              </Tabs>

              {/*    */}
              <div className="mt-6 pt-6 border-t border-border">
                <div className="flex items-center justify-between mb-4">
                  <h4 className="font-medium"> </h4>
                  <Button variant="outline" size="sm">
                    <Star className="w-4 h-4 mr-2" />
                     
                  </Button>
                </div>

                {/*    */}
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-3">
                  {recommendedSpecials.map((special, index) => (
                    <div
                      key={index}
                      className="p-3 border rounded-lg cursor-pointer hover:bg-accent/50 transition-colors"
                    >
                      <div className="flex items-center justify-between mb-2">
                        <span className="font-medium">{special.name}</span>
                        <Badge variant="outline" className="text-xs">
                          {special.season}
                        </Badge>
                      </div>
                      <p className="text-sm text-muted-foreground mb-2">
                        {special.reason}
                      </p>
                      <div className="flex items-center justify-between">
                        <span className="text-xs text-muted-foreground">
                           
                        </span>
                        <span className="text-sm font-medium text-secondary">
                          {special.popularity}%
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>

              {/*   */}
              <div className="mt-6 flex gap-2">
                <Button variant="outline" className="flex-1">
                   
                </Button>
                <Button className="flex-1 gradient-primary text-white border-0">
                   
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>

      {/*   */}
      <Tabs defaultValue="current" className="space-y-4">
        <TabsList className="grid w-full grid-cols-2 max-w-md">
          <TabsTrigger value="current">  </TabsTrigger>
          <TabsTrigger value="history">  </TabsTrigger>
        </TabsList>

        <TabsContent value="current">
          <Card className="elevation-1">
            <CardHeader>
              <CardTitle>8  </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-center">
                <div>
                  <div className="text-2xl font-bold text-secondary">28</div>
                  <div className="text-sm text-muted-foreground"> </div>
                </div>
                <div>
                  <div className="text-2xl font-bold text-warning">3</div>
                  <div className="text-sm text-muted-foreground"> </div>
                </div>
                <div>
                  <div className="text-2xl font-bold text-primary">5</div>
                  <div className="text-sm text-muted-foreground"> </div>
                </div>
                <div>
                  <div className="text-2xl font-bold text-info">90%</div>
                  <div className="text-sm text-muted-foreground"></div>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="history">
          <Card className="elevation-1">
            <CardHeader>
              <CardTitle>  </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {/*    */}
                <div className="flex gap-2">
                  <Input
                    placeholder="   "
                    className="flex-1"
                  />
                  <Select>
                    <SelectTrigger className="w-32">
                      <SelectValue placeholder=" " />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="7">7</SelectItem>
                      <SelectItem value="6">6</SelectItem>
                      <SelectItem value="5">5</SelectItem>
                    </SelectContent>
                  </Select>
                  <Button variant="outline"></Button>
                </div>

                {/*   */}
                <div className="space-y-2">
                  {[
                    { month: "7", completed: 31, satisfaction: 4.2 },
                    { month: "6", completed: 30, satisfaction: 4.0 },
                    { month: "5", completed: 31, satisfaction: 4.1 },
                  ].map((item, index) => (
                    <div
                      key={index}
                      className="p-4 border rounded-lg flex items-center justify-between hover:bg-accent/50 cursor-pointer"
                    >
                      <div>
                        <span className="font-medium">
                          2025 {item.month} 
                        </span>
                        <p className="text-sm text-muted-foreground">
                          {item.completed}  
                        </p>
                      </div>
                      <div className="text-right">
                        <div className="font-medium text-secondary">
                           {item.satisfaction}
                        </div>
                        <div className="text-sm text-muted-foreground">
                           
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}
